.newProductContainer {
    width: 90%;
    box-sizing: border-box;
    background-color: rgb(221, 221, 221);
    border-left: 1px solid rgba(0, 0, 0, 0.175);
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.confirmOrderPage {
    width: 85vw;
    height: 100vh;
}

.newProductContainer h1 {
    color: rgba(0, 0, 0, 0.675);
    font: 350 2rem "Roboto";
    text-align: center;
}

.createProductForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 100%;
    transition: all 500ms;

}


.createProductForm>div {
    display: flex;
    align-items: center;
    width: 100%;

}



.createProductForm>div>input,
.createProductForm>div>select,
.createProductForm>div>textarea {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.275);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}



.createProductForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.5vmax;
    color: rgba(0, 0, 0, 0.675);
}


#createProductFormImages {
    width: 3vmax;
    aspect-ratio: 1/1;
    background-size: cover;
    border-radius: 50%;
    margin: 0 1vmax 0 0;

}

#createProductFormImage {
    width: 50rem;

    display: flex;
    gap: 1rem;

    overflow: auto;
}

#createProductFormImage>img {
    width: 6rem;
    height: 6rem;
    object-fit: cover;

}

#createProductFormFile>input {
    display: flex;
    padding: 0%;
}

#createProductFormFile>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0;
    font: 400 .8vmax cursive;
    transition: all 500ms;
    color: rgba(0, 0, 0, 0.675);
    background-color: rgba(255, 255, 255);
    padding: 0 1vmax;
}

#createProductFormFile>input::file-selector-button:hover {
    background-color: rgb(233, 231, 231);
}



.createProductBtn {
    border: none;
    background-color: tomato;
    color: white;
    width: 100%;
    font: 300 1vmax "Roboto";
    padding: .8vmax;
    border-radius: 4px;
    cursor: pointer;
    transition: all 500ms;

}

.createProductBtn:hover {
    background-color: rgb(175, 60, 40);

}

.orderDetailsContainerBox>p {
    font: 400 1vmax "Roboto";
}


@media screen and (max-width:900px) {



    .newProductContainer {
        background-color: white;
    }

    .createProductForm {
        padding: 5vmax;
    }


    .createProductForm>div>input,
    .createProductForm>div>select,
    .createProductForm>div>textarea {
        padding: 2.5vmax 5vmax;
        font: 300 1.8vmax cursive;
    }


    .createProductForm>div>svg {

        font-size: 2.5vmax;

    }


    .registerPImage {
        width: 8vmax;
        border-radius: 50%;

    }



    #createProductFormFile>input::file-selector-button {
        height: 7vh;
        font: 400 1.8vmax cursive;
    }


    .createProductBtn {
        padding: 1.8vmax;

    }

}