.LoginSignUpContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
}

.LoginSignUpBox {

    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
}

.login_signUp_toggle {
    display: flex;
    height: 3vmax;
}

.login_signUp_toggle>p {
    color: rgba(0, 0, 0, 0.675);
    font: 300 1vmax "Roboto";
    transition: all 0.5s;
    cursor: pointer;
    display: grid;
    place-items: center;
    width: 100%;

}

.login_signUp_toggle>p:hover {
    color: tomato;

}

.LoginSignUpBox>div>button {
    background-color: tomato;
    height: 3px;
    width: 50%;
    border: none;
    transition: all 500ms;
}

.loginForm,
.signUpForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 500ms;
}

.signUpForm {
    background: white;
    transform: translateY(-100%) translateX(-100%);
}


.loginForm>div,
.signUpForm>div {
    display: flex;
    align-items: center;
    width: 100%;

}



.loginForm>div>input,
.signUpForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.275);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}


.loginForm>div>svg,
.signUpForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.5vmax;
    color: rgba(0, 0, 0, 0.675);
}

.loginForm>a {
    color: rgba(0, 0, 0, 0.675);
    text-decoration: none;
    align-self: flex-end;
    transition: all 500ms;
    font: 500 .8vmax "Gill Sans";
}

.loginForm>a:hover {
    color: black
}

.registerPImage {
    width: 3vmax;
    aspect-ratio: 1/1;
    background-size: cover;
    border-radius: 50%;
    margin: 0 1vmax 0 0;

}

#registerImage>input {
    display: flex;
    padding: 0%;
}

#registerImage>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0;
    font: 400 .8vmax cursive;
    transition: all 500ms;
    color: rgba(0, 0, 0, 0.675);
    background-color: rgba(255, 255, 255);
    padding: 0 1vmax;
}

#registerImage>input::file-selector-button:hover {
    background-color: rgb(233, 231, 231);
}


.loginBtn,
.signUpBtn {
    border: none;
    background-color: tomato;
    color: white;
    width: 100%;
    font: 300 1vmax "Roboto";
    padding: .8vmax;
    border-radius: 4px;
    cursor: pointer;
    transition: all 500ms;

}

.loginBtn:hover,
.signUpBtn:hover {
    background-color: rgb(175, 60, 40);

}

.shiftToLeft {
    transform: translateX(-100%);
}

.shiftToRight {
    transform: translateX(100%);
}

.shiftToNeutral {
    transform: translateX(0);
}

.shiftToNeutralForm {
    transform: translateX(0) translateY(-100%);
}

@media screen and (max-width:900px) {
    .LoginSignUpContainer {
        background-color: white;
    }

    .LoginSignUpBox {
        width: 100vw;
        height: 95vh;
    }

    .login_signUp_toggle {
        height: 5vmax;
    }

    .login_signUp_toggle>P {
        font: 300 1.5vmax "Roboto";
    }

    .loginForm, .signUpForm {
        padding: 5vmax;
    }

    .loginForm>div>input,
    .signUpForm>div>input {
        padding: 2.5vmax 5vmax;
        font: 300 1.8vmax cursive;
    }

    .loginForm>div>svg,
    .signUpForm>div>svg {

        font-size: 2.5vmax;

    }

    .loginForm>a {
        font: 500 1.8vmax "Gill Sans";
    }

    .registerPImage {
        width: 8vmax;
        border-radius: 50%;

    }



    #registerImage>input::file-selector-button {
        height: 7vh;
        font: 400 1.8vmax cursive;
    }

    .loginBtn,
    .signUpBtn {
        padding: 1.8vmax;

    }

}