.shippingContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    background-color: rgba(231, 231, 231);

}

.shippingBox {

    background-color: white;
    width: 50vw;
    height: 88vh;
    box-sizing: border-box;
    overflow: hidden;
}

.shippingBox>h2 {
    margin: 1vmax 2vmax;
}




.shippingBox>div>button {
    background-color: tomato;
    height: 3px;
    width: 50%;
    border: none;
    transition: all 500ms;
}


.shippingForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    justify-content: space-evenly;
    padding: 1vmax 2vmax;
    /* gap: 2vmax; */
    height: 80%;
    transition: all 500ms;
}


.shippingForm>div {
    display: flex;
    gap: 1vmax;
    align-items: center;
    width: 100%;
    position: relative;
}

.shippingForm>div>svg {
    font-size: 2vmax;
    position: absolute;
    left: 1vmax;
}



.shippingForm>div>input, .shippingForm>div>select {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.275);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}



.shippingPImage {
    width: 5vmax;
    aspect-ratio: 1/1;
    background-size: cover;
    border-radius: 50%;
    margin: 0 1vmax 0 0;

}

#rshippingImage>input {
    display: flex;
    padding: 0%;
}

#shippingImage>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 4vh;
    border: none;
    margin: 0;
    font: 400 .9vmax cursive;
    transition: all 500ms;
    color: rgba(0, 0, 0, 0.675);
    background-color: rgba(255, 255, 255);
    /* padding: 0 1vmax; */
}

#shippingImage>input::file-selector-button:hover {
    background-color: rgb(233, 231, 231);
}



.shippingBtn {
    border: none;
    background-color: tomato;
    color: white;
    width: 100%;
    font: 300 1vmax "Roboto";
    padding: .8vmax;
    border-radius: 4px;
    cursor: pointer;
    transition: all 500ms;

}


.shippingBtn:hover {
    background-color: rgb(175, 60, 40);

}

.shiftToLeft {
    transform: translateX(-100%);
}

.shiftToRight {
    transform: translateX(100%);
}

.shiftToNeutral {
    transform: translateX(0);
}

.shiftToNeutralForm {
    transform: translateX(0) translateY(-100%);
}

.profile-name {
    padding-bottom: 3px;
    border-bottom: .25vmax solid tomato;

}

@media screen and (max-width:900px) {

    .shippingContainer {

        background-color: white;
    }

    .shippingBox {

        width: 100vw;
        height: 90vh;

    }

    .shippingBox>h2 {
        margin: 4vmax 5vmax;
    }



    .shippingForm {
        padding: 1vmax 5vmax;
    }


    .shippingForm>div>input, .shippingForm>div>select {
        padding: 2vmax 6vmax;
        font: 300 1.8vmax cursive;
    }


    .shippingForm>div>svg {

        font-size: 3vmax;

    }


    .shippingPImage {
        width: 8vmax;
        border-radius: 50%;

    }



    #shippingImage>input::file-selector-button {
        height: 7vh;
        font: 400 1.8vmax cursive;
    }


    .shippingBtn {
        padding: 1.8vmax;
        font-size: 2vmax;

    }

}