.confirmOrderPage {
    width: 100vw;
    max-width: 100%;
    display: grid;
    background-color: white;
    grid-template-columns: 6fr 3fr;
}

.confirmOrderPage>div:last-child {
    border-left: 1px solid rgba(0, 0, 0, 0.250);
}

.confirmShippingArea {
    padding: 5vmax;
    padding-bottom: 0%;
}

.confirmShippingArea>p {
    font: 400 1.8vmax "Roboto";
}

.confirmShippingAreaBox,
.confirmCartItemsContainer {
    margin: 2vmax;
}


.confirmShippingAreaBox>div {
    display: flex;
    margin: 1vmax 0;
}

.confirmShippingAreaBox>div>p {
    font: 450 1.5vmax "Roboto";
    color: black;
}

.confirmShippingAreaBox>div>span {
    font: 400 1.5vmax "Roboto";
    margin: 0 1vmax;
    color: black;
}

.confirmCartItems {
    padding: 5vmax;
    padding-top: 2vmax;
}

.confirmCartItemsContainer {
    max-height: 60vmax;
    overflow-y: auto;
}

.confirmCartItemsContainer>div {
    display: flex;
    font: 400 1vmax "Roboto";
    align-items: center;

    justify-content: space-between;
    margin: 2vmax 0;
}

.confirmCartItemsContainer>div>img {
    width: 6vmax;

}

.confirmCartItemsContainer>div>a {
    width: 60%;
    color: #575757;
    margin: 0 2vmax;
    text-decoration: none;

}

.confirmCartItemsContainer>div>span {
    font: 100 1vmax "Roboto";
    color: #5e5e5e;


}

.orderSummary {
    padding: 7vmax;
}

.orderSummary>p {
    text-align: center;
    font: 400 1.8vmax "Roboto";
    border-bottom: 1px solid rgba(0, 0, 0, 0.250);
    box-sizing: border-box;
    width: 100%;
    margin: auto;
}

.orderSummary>div>div {
    display: flex;
    font: 400 1.2vmax "Roboto";
    justify-content: space-between;
    margin: 2vmax 0;
}

.orderSummary>div>div>span {
    color: rgba(0, 0, 0, 0.650)
}

.orderSummaryTotal {
    display: flex;
    justify-content: space-between;
    font: 300 1vmax solid;
    border-top: 1px solid rgba(0, 0, 0, 0.350);
    padding: 2vmax 0;
}

.orderSummary>button {
    background-color:
        tomato;
    color: white;
    width: 100%;
    width: 100%;
    transition: .5s;
    border: none;
    outline: none;
    border-radius: .5vmax;
    padding: 1vmax 4vmax;
    font: 400 1vmax "Roboto";
    cursor: pointer;
}

.orderSummary>button:hover {
    background-color:
        rgb(170, 33, 9);
}

@media screen and (max-width:900px) {
    .confirmOrderPage {
        grid-template-columns: 1fr;
        height: unset;
    }

    .confirmOrderPage>div:last-child {
        border-left: 0;
        border-top: 1px solid rgba(0, 0, 0, 0.250);
    }

    .confirmShippingArea>p {
        font: 400 6vw "Roboto";
    }

    .confirmShippingAreaBox>div {
        display: flex;
        margin: 6vw 0;
    }

    .confirmShippingAreaBox>div>p {
        font: 400 4vw "Roboto";
    }

    .confirmShippingAreaBox>div>span {
        font: 200 4vw "Roboto";
    }

    .confirmCartItems>p {
        font: 400 6vw "Roboto";


    }

    .confirmCartItemsContainer {
        max-height: 50vw;
    }

    .confirmCartItemsContainer>div {
        font: 400 4vw "Roboto";
        margin: 4vw 0;
    }

    .confirmCartItemsContainer>div>img {
        width: 10vw;
    }

    .confirmCartItemsContainer>div>span {
        font: 100 4vw "Roboto";

    }

    .confirmCartItemsContainer>div>a {
        margin: 0;
        width: 30%;

    }

    .orderSummaryl {
        padding: 12vw;
    }

    .orderSummary>p {
        font: 400 6vw "Roboto";
        padding: 4vw;

    }

    .orderSummary>div>div {
        font: 300 4vw "Roboto";
    }

    .orderSummaryTotal {
        font: 300 4vw "Roboto";
        padding: 5vw 0;
    }

    .orderSummary>button {
        padding: 3vw;
        margin: 4vw auto;
        font: 400 4vw "Roboto";
    }

}